import React from 'react';
import '../styles/Navbar.css';

const Navbar = (props) => {
	const { isDarkMode, handleDarkMode } = props;
	return (
		<div className="Navbar">
			<div className="options">
				<div className="darkModeSlider">
					{isDarkMode ? <i className="fas fa-sun" /> : <i className="fas fa-moon" />}
					<label className="switch">
						<input type="checkbox" onChange={handleDarkMode} defaultChecked={isDarkMode} />
						<span className="slider round" />
					</label>
				</div>
			</div>
			<div className="header">
				<i className="fas fa-crown fa-2x" />
				<h1 className="logo">King Kev</h1>
				<div className="tag">'The unluckiest man in the world'</div>
			</div>

			<hr />
		</div>
	);
};

export default Navbar;
