import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import '../styles/Footer.css';

class Footer extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	render() {
		const { isViewAll, handleToggleState, handleRandom } = this.props;
		return (
			<div>
				{!isViewAll ? (
					<div className="Footer">
						<button className="random-button" onClick={handleRandom}>
							More Keggy
						</button>
						<Link to="/quotes">
							<button className="view-all-button" onClick={handleToggleState}>
								View All
							</button>
						</Link>
						<a className="contact" href="mailto:dan.s.cross@icloud.com">
							Contact the Creator
						</a>
						<span className="footer-text">Kevin Keegan Quote Generator &copy; 2019</span>
					</div>
				) : (
					<div className="Footer">
						<Link to="/">
							<button className="random-button" onClick={handleToggleState}>
								Random Keggy
							</button>
						</Link>
						<a className="contact" href="mailto:dan.s.cross@icloud.com">
							Contact the Creator
						</a>
						<span className="footer-text">Kevin Keegan Quote Generator &copy; 2019</span>
					</div>
				)}
			</div>
		);
	}
}

export default Footer;
